import React from 'react';
import axios from 'axios';
import Movie from '../components/Movie';
import "./Home.css";


class Home extends React.Component {
  
  state = {
    isLoading: true,
    movies: []
  }

  getMovies = async () => {
    // axios  api     JS   .   , async, await . JS   .
    // axios javascript fetch  fetch layer    API  (npm i axios) 
    // async = !, await = ?
    const {data : {data : {movies}}} = await axios.get("https://yts-proxy.now.sh/list_movies.json?sort_by=rating");
    // movies_1 state , movies_2 axios object
    this.setState({movies, isLoading: false});
  }

  componentDidMount() {
    this.getMovies();
  }
  
  render(){
    //  div  this.state.isLoading   ES6  const  
    const { isLoading, movies } = this.state;
    // javascript  - isLoading true loading, false We are ready
    return (
      <section className="container">
        {isLoading ? (
          <div className="loader">
            <span className="loader__text">Loading...</span>
          </div>
        ) : (
          <div className="movies">
            {movies.map(movie => (
              <Movie 
                key={movie.id}
                id={movie.id} 
                year={movie.year} 
                title={movie.title} 
                summary={movie.summary} 
                poster={movie.medium_cover_image} 
                genres={movie.genres}
              />
            )
            )}
          </div>
        )
        }
      </section>
    )
  }
}


export default Home;



